@mixin body-position {
    max-width: $wmax ;
    min-width: $wmin;
    margin: auto;
}

@mixin boxShadow {
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25), 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}

@mixin boxMoreShadow {
    box-shadow: 0px 4px 4px 0px rgba(19, 23, 62, 0.70), 0px 4px 4px 0px rgba(0, 0, 0, 0.50);
}

@mixin WH-full {
    width: 100%;
    height: 100%;
}

@mixin font($size) {
    font-size: $size;
    font-style: normal;
    line-height: 142.6%;
}




@mixin flex-end {
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

@mixin flex-column {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

@mixin flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

@mixin flex-space {
    display: flex;
    justify-content: space-around;
    align-items: center;
    
}

@mixin small-radius {
    border-radius: 10px;
}

@mixin big-radius {
    border-radius: 15px;
}

@mixin font-right {
    font-size: $fsizeHead3;
    font-style: normal;
    text-align: right;
    line-height: 142.6%;
}

@mixin small-button {
    width: 250px;
    height: 50px;
    flex-shrink: 0;
    background: $primary-color;
    color: $tertiary-color;
    text-decoration: none;
    font-family: 'Amatic SC', cursive;
    font-size: 35px;
    margin-top: 30px;
}



    @mixin rightArrow-style {
        display: flex;
        position: absolute;
        top: 47%;
        right: 2%;
        width: 12px;
        height: 20px;
        z-index: 10;
        cursor: pointer;
    
        @media screen and (min-width: $breakpoint-desktop) {
            width: 47px;
            height: 79px;
            top: 42%;
        }
    }
    
    @mixin leftArrow-style {
        display: flex;
        position: absolute;
        top: 47%;
        left: 2%;
        width: 12px;
        height: 20px;
        z-index: 10;
        cursor: pointer;
    
        @media screen and (min-width: $breakpoint-desktop) {
            width: 47px;
            height: 79px;
            top: 42%;
        }
    }